@font-face {
    font-family: "Inter";
    src: url(/mesto-react/static/media/Inter-Regular.dc131113.woff2) format("woff2"), url(/mesto-react/static/media/Inter-Regular.3ac83020.woff) format("woff");
    font-style: normal;
    font-weight: 400;
}

@font-face {
    font-family: "Inter";
    src: url(/mesto-react/static/media/Inter-Medium.46d7404e.woff2) format("woff2"), url(/mesto-react/static/media/Inter-Medium.5ce3e4db.woff) format("woff");
    font-style: normal;
    font-weight: 500;
}

@font-face {
    font-family: "Inter";
    src: url(/mesto-react/static/media/Inter-Black.661569af.woff2) format("woff2"), url(/mesto-react/static/media/Inter-Black.d0b121f3.woff) format("woff");
    font-style: normal;
    font-weight: 900;
}
.page {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  min-height: 100vh;
  background-color: #000000;
  color: white;
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}
.main {
  max-width: 880px;
  margin: 0 auto;
}

@media screen and (max-width: 320px) {
  .main {
    max-width: 282px;
  }
}
.header {
  max-width: 880px;
  margin: 0 auto 40px;
  padding-top: 45px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);

}

@media screen and (min-width:321px) and (max-width: 910px) {
  .header {
    max-width: 90%;
  }
}

@media screen and (max-width: 320px) {
  .header {
    max-width: 320px;
    padding-top: 28px;
    margin-bottom: 0;
  }
}
.header__logo {
  max-width: 142px;
  padding-bottom: 38px;
}

@media screen and (max-width: 320px) {
  .header__logo {
    max-width: 103.74px;
    padding-bottom: 31.6px;
    margin-left: 27px;
  }
}
.button:hover {
  opacity: .6;
  cursor: pointer;
}
.profile {
  width: 100%;
  margin: 0 auto 50px;
  display: grid;
  grid-template-columns: 120px 1fr 150px;
  grid-gap: 30px;
  gap: 30px;
  justify-content: space-between;
}

@media screen and (max-width: 510px) {
  .profile {
    grid-template-columns: 1fr;
    grid-gap: 26px;
    gap: 26px;
    margin-top: 42px;
    margin-bottom: 36px;
  }
}
.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}

@media screen and (max-width: 510px) {
  .profile__avatar {
    margin: 0 auto;
  }
}
.profile__avatar-button {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    position: absolute;
    opacity: 0;
    border: 0;
    background-repeat: no-repeat;
    background-position: center;
    transition: .8s ease;
  }
  
  .profile__avatar-button:hover {
    background-image: url(/mesto-react/static/media/edit-avatar.d476a8b8.svg);
    opacity: .8;
    background-color: #000;
  }
.profile__button-edit {
  width: 24px;
  height: 24px;
  background-image: url(/mesto-react/static/media/pencil.6bfdb354.svg);
  background-color: transparent;
  background-size: 10px;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid white;
  align-self: flex-end;
  margin-bottom: 10px;
}

@media screen and (max-width: 510px) {
  .profile__button-edit {
    width: 18px;
    height: 18px;
    background-size: 7.5px;
    padding: 0;
    margin-right: 19px;
    margin-bottom: 4px;
  }
}
.profile__button-add {
  width: 150px;
  height: 50px;
  background-image: url(/mesto-react/static/media/Add-Button.b2fce4ac.svg);
  background-size: 22px;
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
  border: 2px solid white;
  margin-top: 35px;
  border-radius: 2px;
}

@media screen and (max-width: 510px) {
  .profile__button-add {
    width: 282px;
    background-size: 16px;
    margin: 0 auto;
  }
}
.profile__card {
    display: flex;
  }
  
  @media screen and (max-width: 480px) {
    .profile__card {
      flex-direction: column;
    }
  }
.profile__info {
  max-width: 336px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 6px 18px;
  gap: 6px 18px;
}

@media screen and (max-width: 510px) {
  .profile__info {
    max-width: 282px;
    margin: 0 auto 10px;
    grid-gap: 6px 10px;
    gap: 6px 10px;
  }
}
.profile__subtitle {
  max-width: 336px;
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 510px) {
  .profile__subtitle {
    max-width: 282px;
    font-size: 14px;
    grid-area: 2 / 1 / span 1 / span 2;
    text-align: center;
    margin: auto;
  }
}
.profile__title {
  max-width: 294px;
  font-size: 42px;
  line-height: 1.14;
  font-weight: 500;
  margin-top: 20px;
  margin-bottom: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 510px) {
  .profile__title {
    max-width: 189px;
    font-size: 27px;
    line-height: 1.2;
    margin: 0 auto auto;
    padding-left: 48px;
  }
}
.elements {
  width: 100%;
  display: grid;
  grid-gap: 20px 17px;
  gap: 20px 17px;
  grid-template-columns: repeat(auto-fit, 282px);
  margin: 0 0 67px;
  justify-content: center;
}

@media screen and (max-width: 320px) {
  .elements {
    margin-bottom: 48px;
  }
}
.element {
  color: black;
  background-color: white;
  max-width: 282px;
  border-radius: 10px;
  position: relative;
}
.element__button-like {
  width: 22px;
  height: 19px;
  background-image: url(/mesto-react/static/media/Vector.00b1c22a.svg);
  border: none;
  background-color: transparent;
}

.element__button-like:hover {
  opacity: .5;
  cursor: pointer;
}
.element__button-like_is-active{
  background-image: url(/mesto-react/static/media/likeblack.302bdf76.svg);
}
.element__button-delete {
    width: 18px;
    height: 19px;
    padding: 0;
    position: absolute;
    align-self: center;
    top: 10px;
    right: 10px;
    background-image: url(/mesto-react/static/media/delete.4220563a.svg);
    border: none;
    background-color: transparent;
   z-index: 2;
  }
  
  .element__button-delete:hover {
    opacity: .6;
    cursor: pointer;
  }
.element__img {
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 282px;
}
.element__like-container {
  max-width: 30px;
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 3px;
  gap: 3px;
  justify-content: center;
}
.element__like-count{
  line-height: 1.21;
  height: 13px;
  width: 10px;
  text-align: center;
  margin: 0;
  padding: 0;
}
.element__title {
  max-width: 215px;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.21;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.element__info {
  max-width: 240px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin: 25px auto;
}
.footer {
  max-width: 880px;
  margin: 0 auto;
}

@media screen and (max-width: 910px) {
  .footer {
    max-width: 90%;
  }
}
.footer__autor {
  color: #545454;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1;
  margin: 0;
  padding-bottom: 60px;
}

@media screen and (max-width: 320px) {
  .footer__autor {
    font-size: 14px;
    padding-bottom: 36px;
  }
}
.popup {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
  align-items: center;
  justify-content: center;
  z-index: 3;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s, opacity 0.2s ease-in;
}
.popup_photo-card{
  background-color: rgba(0, 0, 0, 0.9);
}
.popup__container {
  width: 430px;
  background-color: white;
  color: black;
  position: relative;
  z-index: 2;
  border-radius: 10px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 510px) {
  .popup__container {
    width: 282px;
  }
}
.popup__container-card{
  max-width: 100%;
  max-height: 100%;
  position: relative;
}
.popup__input {
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 1;
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding-top: 13px;
    padding-bottom: 13px;
    margin-top: 27px;
}
.popup__input_type_error{
  border-bottom-color: red;
}
.popup__input-error_active{
  font-size: 12px;
  line-height: 1;
  color: #FF0000;
  margin-top: 5px;
  position: absolute;
}
.popup__profile {
  font-weight: 900;
  font-size: 24px;
  line-height: 0.96;
  padding-bottom: 24px;
  margin: 0;
}

@media screen and (max-width: 510px) {
  .popup__profile {
    font-size: 18px;
    line-height: 1.22;
    padding-bottom: 45px;
  }
}
.popup__form {
  width: 358px;
  padding-top: 34px;
  padding-bottom: 37px;
}

@media screen and (max-width: 510px) {
  .popup__form {
    width: 238px;
    padding-top: 25px;
    padding-bottom: 25px;
  }
}
.popup__button-save {
  width: 358px;
  height: 50px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 2px;
  margin-top: 48px;
  font-size: 18px;
  padding: 0;
}

@media screen and (max-width: 510px) {
  .popup__button-save {
    width: 238px;
    height: 46px;
    margin-top: 45px;
    font-size: 14px;
  }
}

.popup__button-save:hover {
  opacity: .8;
  cursor: pointer;
}
.popup__button-save_disabled{
    background-color: transparent;
    color: rgba(0, 0, 0);
    opacity: .2;
    border: 1px solid black;
}

.popup__button-save_disabled:hover {
    cursor: default;
    opacity: .2;
  }
.popup__button-close {
  background: url(/mesto-react/static/media/Close-Icon.cb0f2bbf.svg);
  background-size: cover;
  width: 32px;
  height: 32px;
  border: none;
  position: absolute;
  top: -40px;
  right: -40px;
}

@media screen and (max-width: 510px) {
  .popup__button-close {
    width: 20px;
    height: 20px;
    right: 0;
    top: -36px;
  }
}
.popup_opened {
  visibility: visible;
  opacity: 1;
}
.popup__img-card{
  max-width: 75vw;
  max-height: 75vh;
  margin: 0 auto;
  border: none;
}
.popup__title-card{
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  padding: 0;
  color: #FFFFFF;
  margin: 0;
  padding-top: 10px;
}

